.loader {
  margin: 0 auto;
  width: 100px;
  height: 100px;
  border-radius: 50px;
  border: 3px dashed #ea5400;
  animation: rotate 1s infinite linear;
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }

  to {
    transform: rotate(360deg) scale(1.4);
  }
}